/**
 * Lato
 */
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 100;
  src: url("/fonts/Lato-Thin.ttf") format("truetype"), url("/fonts/Lato-Thin.woff") format("woff"); }

@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 300;
  src: url("/fonts/Lato-Light.ttf") format("truetype"), url("/fonts/Lato-Light.woff") format("woff"); }

@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/Lato-Regular.ttf") format("truetype"), url("/fonts/Lato-Regular.woff") format("woff"); }

@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 700;
  src: url("/fonts/Lato-Bold.ttf") format("truetype"), url("/fonts/Lato-Bold.woff") format("woff"); }

@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 900;
  src: url("/fonts/Lato-Black.ttf") format("truetype"), url("/fonts/Lato-Black.woff") format("woff"); }

* {
  box-sizing: border-box; }

html, body {
  height: 100%;
  width: 100%; }

body {
  margin: 0;
  font-family: "Lato", sans-serif; }

h1, h2, h3, h4, h5, h6, p, a, span, button, input, select, textarea {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  color: #ffffff; }

a, button, select, input {
  outline: 0 !important;
  box-shadow: none !important; }

a:hover, a:focus, a:active, a:target {
  text-decoration: none; }

