.header-top {
  width: 100%;
  height: 70px;
  background-color: #c0392b; }
  .header-top > div {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center; }
  .header-top input#username {
    margin-right: 10px;
    width: 200px; }
  .header-top input#btnUploadImage {
    margin-right: 10px; }

.btn {
  transition: all .2s ease-in-out; }

#gallery {
  width: 100%;
  height: 100%;
  background-color: #2c3e50;
  position: relative;
  padding: 30px 15px; }
  #gallery .gallery-info {
    font-weight: 300;
    color: #ffffff;
    width: 100%;
    text-align: center;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%); }
  #gallery .gallery-img img {
    width: 100%;
    height: auto; }
    #gallery .gallery-img img:hover {
      cursor: pointer; }
  #gallery .gallery-img .newComment {
    color: yellow; }
  #gallery .gallery-img .imageAuthor {
    margin-top: 5px; }
  #gallery .gallery-img .imageAuthor, #gallery .gallery-img .imageUploadDate {
    font-size: 12px;
    color: #ffffff; }

#content {
  width: 100%;
  height: calc(100% - 70px);
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  text-align: center; }
  #content #imageComments {
    display: none;
    width: 100%;
    height: 100%;
    background-color: #114183;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    z-index: 1000; }
    #content #imageComments #closeComments {
      width: 25px;
      height: 25px;
      float: right;
      font-weight: bold;
      font-size: 16px;
      background-color: #f00;
      border: none;
      color: #ffffff; }
      #content #imageComments #closeComments:hover {
        cursor: pointer; }
    #content #imageComments h1 {
      display: flex;
      justify-content: center;
      margin-top: 20px; }
    #content #imageComments #leftSide {
      width: 49%;
      height: 70%;
      margin-top: 4%;
      float: left; }
      #content #imageComments #leftSide img#imageBig {
        border: 1px solid #ffffff;
        width: 94%;
        height: auto;
        margin: 20px 0 0 30px;
        float: left; }
    #content #imageComments #rightSide {
      width: 49%;
      height: 70%;
      margin-top: 4%;
      float: right;
      text-align: left; }
      #content #imageComments #rightSide input#comment {
        height: 30px;
        margin-left: 15px; }
      #content #imageComments #rightSide input#send {
        height: 34px;
        padding: 0 15px;
        margin-left: 3px; }
        #content #imageComments #rightSide input#send:hover {
          cursor: pointer; }
      #content #imageComments #rightSide #allComments {
        display: block;
        width: 85%;
        height: 80%;
        float: left;
        margin-top: 20px;
        margin-left: 40px;
        overflow-x: hidden; }
        #content #imageComments #rightSide #allComments h3 {
          text-decoration: underline; }
        #content #imageComments #rightSide #allComments p {
          font-size: 14px;
          margin-left: 5px;
          margin-top: 2px; }
          #content #imageComments #rightSide #allComments p:first-child {
            margin-top: 6px; }
          #content #imageComments #rightSide #allComments p span {
            font-weight: bold;
            margin-right: 8px; }
  #content #uploadBox {
    display: none;
    width: 400px;
    height: 200px;
    border: 1px solid #000000;
    background-color: #114183;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto; }
    #content #uploadBox #closeUpload {
      width: 25px;
      height: 25px;
      float: right;
      font-weight: bold;
      font-size: 16px;
      background-color: #f00;
      border: none;
      color: #ffffff; }
      #content #uploadBox #closeUpload:hover {
        cursor: pointer; }
    #content #uploadBox h1 {
      display: flex;
      justify-content: center;
      margin-top: 30px; }
    #content #uploadBox #selectFileInput {
      margin-top: 30px;
      margin-left: 80px;
      width: 200px;
      color: #ffffff; }
    #content #uploadBox #uploadImageInput {
      color: #000000; }
    #content #uploadBox #uploadFileBtn {
      width: 200px;
      margin-top: 6px;
      margin-left: 80px;
      height: 25px;
      display: flex;
      justify-content: center;
      align-self: center;
      text-align: center; }
  #content #logInBox {
    display: none;
    width: 400px;
    height: 200px;
    border: 1px solid #000000;
    background-color: #114183;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto; }
    #content #logInBox #closeLogIn {
      width: 25px;
      height: 25px;
      float: right;
      font-weight: bold;
      font-size: 16px;
      background-color: #f00;
      border: none; }
      #content #logInBox #closeLogIn:hover {
        cursor: pointer; }
    #content #logInBox h1 {
      display: flex;
      justify-content: center;
      margin-top: 30px; }
    #content #logInBox .loginInputs {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 10px; }
      #content #logInBox .loginInputs input {
        width: 200px;
        height: 30px; }
        #content #logInBox .loginInputs input:nth-child(2) {
          margin-top: 5px; }
      #content #logInBox .loginInputs input[type='submit'] {
        width: 100px;
        margin-top: 5px;
        border: none; }
        #content #logInBox .loginInputs input[type='submit']:hover {
          cursor: pointer;
          background-color: #5f7cdd;
          color: #ffffff; }
